import React, { useState } from "react";
import { Field, reduxForm } from "redux-form";
import StarRatingComponent from "react-star-rating-component";
import { connect } from "react-redux";

import styles from "./ReviewForm.module.css";
import { getRating } from "../../../redux/selectors";
import { setRating } from "../../../redux/reviewsReducer";
import { textarea } from "../renderFields";

let ReviewForm = ({
  rating,
  setRating,
  pristine,
  submitting,
  handleSubmit,
}) => {
  let [fakeRating, setFakeRating] = useState(rating);
  const onStarClick = (nextValue) => setRating(nextValue);
  const onStarHover = (nextValue) => {
    return setFakeRating(nextValue);
  };
  const onStarHoverOut = () => {
    return setFakeRating(rating);
  };

  return (
    <form onSubmit={handleSubmit} className={styles.reviewForm}>
      <table>
        <tbody>
          <tr>
            <th> :</th>
            <td>
              <StarRatingComponent
                name="rate"
                value={fakeRating || rating}
                starColor="red"
                emptyStarColor="#adadad"
                onStarClick={onStarClick}
                onStarHover={onStarHover}
                onStarHoverOut={onStarHoverOut}
                className={styles.stars}
              />
              <Field name="rating" component="input" type="hidden" />
            </td>
          </tr>
          <tr>
            <th>:</th>
            <td>
              <Field
                id="advantages"
                name="advantages"
                placeholder="  "
                component={textarea}
              />
            </td>
          </tr>
          <tr>
            <th>:</th>
            <td>
              <Field
                id="disadvantages"
                name="disadvantages"
                placeholder="   "
                component={textarea}
              />
            </td>
          </tr>
          <tr>
            <th>:</th>
            <td>
              <Field
                id="comment"
                name="comment"
                placeholder="     "
                component={textarea}
              />
            </td>
          </tr>
          <tr>
            <th> :</th>
            <td>
              <label>
                <Field
                  name="buyType"
                  value="1"
                  component="input"
                  type="radio"
                />
                {` -    `}
              </label>
              <label>
                <Field
                  name="buyType"
                  value="2"
                  component="input"
                  type="radio"
                />
                {` -   `}
              </label>
              <label>
                <Field
                  name="buyType"
                  value="3"
                  component="input"
                  type="radio"
                />
                {` -  (),    `}
              </label>
              <p>
                      
                   .
              </p>
              <button
                onClick={() => setFakeRating(0)}
                disabled={pristine || submitting}
              >
                
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </form>
  );
};

ReviewForm = reduxForm({
  form: "ReviewForm",
  enableReinitialize: true,
  keepDirtyOnReinitialize: true,
})(ReviewForm);

const mapStateToProps = (state) => {
  return {
    rating: getRating(state),
    initialValues: {
      rating: getRating(state),
      buyType: "3",
    },
  };
};

export default connect(mapStateToProps, { setRating })(ReviewForm);
