import React from "react";

import styles from "./Notices.module.css";
import InfoSVG from "../SVG/InfoSVG";
import PropTypes from "prop-types";

const Notices = ({ type }) => {
  const red = "red";
  const green = "green";
  const blue = "#2196f3";

  let notice;
  let color = blue;

  switch (type) {
    case "accessDenied":
      notice =
        "      .   .";
      color = red;
      break;
    case "regSuccess":
      notice =
        "!   !       .";
      color = green;
      break;
    case "regFailed":
      notice = "! -   ,  .";
      color = red;
      break;
    case "profileUpdateSuccess":
      notice = "   ";
      color = green;
      break;
    case "profileUpdateFailed":
      notice = "    -   ...";
      color = green;
      break;
    default:
      notice = type;
  }

  return (
    <div className={styles.notices}>
      <InfoSVG color={color} />
      {notice}
    </div>
  );
};

Notices.propTypes = {
  type: PropTypes.string,
};

export default Notices;
