import React from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";

import styles from "../ReviewForm/ReviewForm.module.css";
import { getReviews } from "../../../redux/selectors";
import { required } from "../validate";
import { textarea } from "../renderFields";

let ReviewAnswerForm = ({ pristine, submitting, handleSubmit }) => {
  return (
    <form onSubmit={handleSubmit} className={styles.reviewForm}>
      <table>
        <tbody>
          <tr>
            <th>:</th>
            <td>
              <Field
                id="comment"
                name="comment"
                placeholder="  "
                component={textarea}
                validate={[required]}
              />
            </td>
          </tr>
          <tr>
            <th> :</th>
            <td>
              <label>
                <Field
                  name="buyType"
                  value="1"
                  component="input"
                  type="radio"
                />
                {` -    `}
              </label>
              <label>
                <Field
                  name="buyType"
                  value="2"
                  component="input"
                  type="radio"
                />
                {` -   `}
              </label>
              <label>
                <Field
                  name="buyType"
                  value="3"
                  component="input"
                  type="radio"
                />
                {` -  (),    `}
              </label>
              <p>
                     
                    .
              </p>
              <button disabled={pristine || submitting}></button>
            </td>
          </tr>
        </tbody>
      </table>
    </form>
  );
};

ReviewAnswerForm = reduxForm({
  form: "ReviewAnswerForm",
  enableReinitialize: true,
  keepDirtyOnReinitialize: true,
  destroyOnUnmount: true,
})(ReviewAnswerForm);

const mapStateToProps = (state) => {
  return {
    reviews: getReviews(state),
    initialValues: {
      buyType: "3",
    },
  };
};

export default connect(mapStateToProps)(ReviewAnswerForm);
